.cover-container {
  max-width: 42em;
}

.body-div {
  text-shadow: 0 0.05rem 0.1rem rgba(0, 0, 0, 0.5);
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, 0.5);
  font-size: 1em;
  scroll-behavior: smooth;
}

section {
  height: 90vh;
  position: relative;
}

section > div {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 100%;
}
